import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "home",
    component: Home
  },
  {
    path: "/account",
    name: "account",
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () =>
      import(/* webpackChunkName: "account" */ "../views/microservices/Account.vue")
  },
  {
    path: "/chat",
    name: "chat",
    component: () => import( "../views/microservices/Chat.vue")
  },
  {
    path: "/events",
    name: "events",
    component: () => import( "../views/microservices/Events.vue")
  },
  {
    path: "/feedbacks",
    name: "feedbacks",
    component: () => import( "../views/microservices/Feedbacks.vue")
  },
  {
    path: "/login",
    name: "login",
    component: () => import( "../views/microservices/Login.vue")
  },
  {
    path: "/logs",
    name: "logs",
    component: () => import( "../views/microservices/Logs.vue")
  },
  {
    path: "/microlearnings",
    name: "microlearnings",
    component: () => import( "../views/microservices/Microlearnings.vue")
  },
  {
    path: "/participants",
    name: "participants",
    component: () => import( "../views/microservices/ParticipantsList.vue")
  },
  {
    path: "/planer",
    name: "planer",
    component: () => import( "../views/microservices/Planer.vue")
  },
  {
    path: "/settings",
    name: "settings",
    component: () => import( "../views/microservices/Settings.vue")
  }
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes
});

export default router;
